.offer {
    display: flex;
    place-items: center;
    flex-direction: column;
    background: #F4E1D3;
    padding-top: 3rem;
}
.ads-container {
    display: flex;
    gap: 3rem;
    height: 30vh;
}

@media screen and (max-width: 1023px) {
   .offer {
        place-items: center;
        flex-direction: column;
    }
    .ads-container {
        gap: 1rem;
        transform: scale(0.7);
    } 
}

@media screen and (max-width: 590px) {
    .offer {
        gap: 0;
        height: 100%;
    }
    .ads-container {
        margin-top: -7rem;
        flex-direction: column;
        height: 100%;
        gap: 1.5rem;
        transform: scale(0.6);
    }
}